export const throwErr = (code:number, response:any) => {
    let message = ''
    switch (code) {
      case 400:
        message = ''
        break
      case 401:
        message = ''
        break
      case 403:
        message = ''
        break
      case 404:
        message = `: ${response.config.url}`
        break
      case 408:
        message = ''
        break
      case 500:
        message = ''
        break
      case 501:
        message = ''
        break
      case 502:
        message = ''
        break
      case 503:
        message = ''
        break
      case 504:
        message = ''
        break
      case 505:
        message = 'HTTP'
        break
      default:
    }
    return message
  }