<template>
  <div class="home">
    <el-card shadow="never">
      <div class="copy-funtion">
        <div>
          <el-button plain @click="SearchTime(1)"></el-button>
          <el-button plain @click="SearchTime(2)"></el-button>
          <el-button plain @click="SearchTime(3)"></el-button>
          <el-button plain @click="SearchTime(4)">30</el-button>
          <el-date-picker
            class="time"
            v-model="SelTime"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="SearchTime(5)"
          ></el-date-picker>
        </div>
        <div class="copy-funtion">
          <el-button class="dowload" @click="dow=true">
            
            <i class="el-icon-download el-icon--right"></i>
          </el-button>
          <el-input placeholder="" v-model="search" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="Search()"></el-button>
          </el-input>
        </div>
      </div>
    </el-card>
    <el-card shadow="never" class="copy-center">
      <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{prop: 'date', order: 'descending'}"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column type="expand" width="55">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.data.url }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="data.url" align="center" label="" width="430"></el-table-column>
        <el-table-column
          prop="created_at"
          align="center"
          label=""
          sortable
          :formatter="formatterTime"
          width="180"
        ></el-table-column>
        <el-table-column prop="data.ip" align="center" label="IP"></el-table-column>
        <el-table-column
          prop="data.address"
          align="center"
          label=""

        ></el-table-column>
                  <!-- :formatter="formatterAddress" -->
        <el-table-column prop="data.search" align="center" label=""></el-table-column>
        <el-table-column prop="data.wechar" align="center" label=""></el-table-column>

        <el-table-column align="center" label="" width="400">
          <template slot-scope="props">
            <el-tag v-if="props.row.data.isiPad">iPad</el-tag>
            <el-tag v-else-if="props.row.data.iPhone">iPhone</el-tag>
            <el-tag v-else-if="props.row.data.isAndroidTablet"></el-tag>
            <el-tag v-else-if="props.row.data.isAndroid">Android</el-tag>

            <el-tag v-if="props.row.data.isWindows">Window</el-tag>
            <el-tag v-else-if="props.row.data.isLinux">Linux</el-tag>
            <el-tag v-else-if="props.row.data.isTablet"></el-tag>
            <el-tag v-else-if="props.row.data.isMac"></el-tag>

            <!-- <el-tag type="warning"></el-tag>
            <el-tag type="danger"></el-tag>-->
            <!-- <el-tag type="success"></el-tag> -->
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-card shadow="never" class="user-list-utils">
      <div class="user-list-utils-flex">
        <div>
          <el-button type="danger" plain @click="DelCopy()"></el-button>
        </div>
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.currentPage"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="this.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.count"
          ></el-pagination>
        </div>
      </div>
    </el-card>

    <el-dialog title="" :visible.sync="dow">
      <el-form ref="form" label-width="80px">
        <el-form-item label="">
          <el-date-picker
            v-model="dowTime"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item label="">
          <el-col :span="4">
            <el-input
              placeholder=""
              v-model="dowNum"
              type="number"
              @change="dowNum>0?dowNum:dowNum=1"
            ></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="">
          <el-col :span="8">
            <el-input
              placeholder=""
              v-model="dowUrl"
              type="text"
            ></el-input>
          </el-col>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dow = false"> </el-button>
        <el-button type="primary" @click="download()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import Component from "vue-class-component";
import moment from "moment";
import excel from "excel-export";
import sever from "file-saver";

@Component
export default class copy extends Vue {
  time = {
    start: 0,
    end: moment()
  };
  SelTime = [];
  search = "";

  // 
  count = 0;
  // 
  tableData = [];
  // 
  pageSize = 10;
  // 
  currentPage = 1;

  // 
  multipleSelection = [];

  // 
  dow = false;
  dowTime = [new Date() - 60 * 60 * 24 * 30 * 1000, new Date()];
  dowNum = 1000;
  dowUrl = "";

  created() {
    this.getCopy();
  }

  // 
  getCopy(pageSize = 10, currentPage = 1, search = "", time) {
    this.$api
      .GteCopy({
        params: {
          pageSize,
          currentPage,
          search,
          time
        }
      })
      .then(v => {
        const { count, rows } = v.data;
        this.count = count;
        this.tableData = rows;
      });
  }
  // 
  formatterTime(row) {
    return moment(row.created_at).format("YYYY-MM-DD, HH:mm:ss");
  }
  // formatterAddress(row) {
  //   return row.data.regionName + "" + row.data.city;
  // }
  // 
  handleSizeChange(val) {
    this.pageSize = val;
    this.getCopy(val, this.currentPage, this.search, this.time);
  }
  handleCurrentChange(val) {
    this.currentPage = val;
    this.getCopy(this.pageSize, val, this.search, this.time);
  }
  // 
  Search() {
    this.getCopy(this.pageSize, this.currentPage, this.search, this.time);
  }
  SearchTime(val) {
    switch (val) {
      case 1:
        this.time.start = moment().startOf("day");
        this.time.end = moment();
        break;
      case 2:
        this.time.start = moment()
          .subtract(1, "days")
          .startOf("day");
        this.time.end = moment().startOf("day");
        break;
      case 3:
        this.time.start = moment()
          .subtract(7, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      case 4:
        this.time.start = moment()
          .subtract(30, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      default:
        this.time.start = moment(this.SelTime[0]);
        this.time.end = moment(this.SelTime[1]);
        break;
    }
    this.currentPage = 1;
    this.Search();
  }

  // 
  handleSelectionChange(val) {
    this.multipleSelection = val;
  }
  // 
  DelCopy() {
    if (this.multipleSelection.length) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          const vid = this.multipleSelection.map(v => (v = v.vid));
          console.log(vid);

          // this.$message({
          //   type: "success",
          //   message: "!"
          // });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    } else {
      this.$message({
        type: "info",
        message: ""
      });
    }
  }
  // // 
  // ResetAndreFresh(val) {
  //   switch (val) {
  //     case 0:
  //       this.time = {
  //         start: 0,
  //         end: moment()
  //       };
  //       this.getCopy();
  //       this.$message({ message: "", type: "success" });
  //       break;
  //     case 1:
  //       this.Search();
  //       this.$message({ message: "", type: "success" });
  //       break;
  //     default:
  //       break;
  //   }
  // }

  // 
  download() {
    let time = {};
    time.start = moment(this.dowTime[0]);
    time.end = moment(this.dowTime[1]);

    this.$api
      .GteCopy({
        params: {
          pageSize: this.dowNum,
          currentPage: 1,
          search: this.dowUrl,
          time
        }
      })
      .then(async v => {
        let conf = {};
        conf.name = "CopyList";
        let alldata = [];
        for (let i = 0; i < v.data.rows.length; i++) {
          let arr = new Array();
          arr.push(
            moment(v.data.rows[i].created_at).format("YYYY-MM-DD, HH:mm:ss")
          );
          arr.push(v.data.rows[i].data.ip);
          arr.push(v.data.rows[i].data.regionName);
          arr.push(v.data.rows[i].data.city);
          arr.push(v.data.rows[i].data.search);
          arr.push(v.data.rows[i].data.url);
          arr.push(v.data.rows[i].data.wechar);
          arr.push(
            moment(v.data.rows[i].data.created_at).format(
              "YYYY-MM-DD, HH:mm:ss"
            )
          );
          alldata.push(arr);
        }
        conf.cols = [
          {
            caption: "",
            type: "string"
          },
          {
            caption: "ip",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          },
          {
            caption: "",
            type: "string"
          }
        ];
        conf.rows = alldata;
        let result = excel.execute(conf);
        let data = new Buffer(result, "binary");
        result = new Blob([data], { type: "application/vnd.openxmlformats" });
        sever.saveAs(result, ".xlsx");
        this.dow = false;
      });
  }
}
</script>

<style scoped>
.time {
  margin: 0 15px;
}
.copy-funtion {
  display: flex;
  justify-content: space-between;
}
.user-list-utils-flex {
  display: flex;
  justify-content: space-between;
}
.el-card {
  margin-bottom: 20px;
  overflow-y: initial;
}
.el-card::-webkit-scrollbar {
  width: 5px;
}
.el-card::-webkit-scrollbar-thumb {
  background: #999;
  border-radius: 10px;
}
.copy-center {
  max-height: 570px;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table {
  position: relative;
}
.dowload {
  margin-right: 20px;
}
.time-select {
  margin-bottom: 20px;
}
</style>